.sidebar{
    background-color: $color-4;
    font-family: $font-1;
    height: 100%;
    min-height: $minHeight;
    width: 264px;
    border-radius: $borderRadius;
    position: relative;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 250px 1fr 108px;
    grid-template-areas: 
    '1'
    '2'
    '3';

    .id{
        grid-area: 1;
        text-align: center;
        user-select: none;
        position: relative;

        .idContent {
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 100%;

            img {
                width: 130px;
                border-radius: 50% ;
                box-shadow: 0 0 0 4px $color-5, 0 0 0 8px $color-4, 0 0 0 12px $white;
            }
            h3 {
                font-size: 1.3rem;
                text-transform: uppercase;
                letter-spacing: .2rem;
                margin-top: 1.5rem;
                color: $white;
            }
        }
    }
    .navigation {
        grid-area: 2;
        padding: 2rem 1rem;

            ul {
                height: 100%;
                display: grid;

                li {
                    padding: 1rem 0 1rem .4rem;
                    font-size: 1.3rem;
                    position: relative;

                    &:hover i {
                        transform: translate(3px, 3px);
                    }

                    a{
                        color: $white;

                        &:hover {
                            background: $color-2;
                            padding: 5px;
                            border-radius: $borderRadius;
                            transition: .3s ease;
                        }
                    }
                    .active {
                        color: $color-1;
                        background-color: $color-5;
                        padding: 5px;
                        border-radius: $borderRadius;

                        &:hover{
                            background: $color-5;
                        }
                    }
                    i {
                        position: absolute;
                        font-size: .95rem;
                        transform: translateY(3px);
                        transition: .2 ease;
                    }
                    span {
                        margin-left: 2.7rem;
                    }
                }
            }
        }
    }
.socialNetwork {
    grid-area: 3;

    ul {
        display: flex;
        width: 264px;
        padding: 0 14px;
        justify-content: space-between;

        li {
            font-size: 1.5rem;
            background: $color-5;
            border-radius: 50%;
            padding: .5rem .8rem;
            transition: 0.3s;

            &:nth-child(3){
                padding: .5rem .75rem;
            }
            &:hover {
                background: $color-2;
                transition: .7s ease;
            }
        }
    }
    .signature{
        margin-top: 2rem;
        text-align: center;
        letter-spacing: .1rem;
        font-style: .7rem;
    }
    p {
        color: $white;

        &:hover {
            color: $color-5;
            transition: .7s ease;
            cursor: default;
        }
    }
}
@media screen and (max-width: 850px) {
    .sidebar {
        display: flex;
        min-height: 50px;
        margin-bottom: .2rem;
        width: 100%;

        .id, .socialNetwork {
            display: none;
        }
        .navigation {
            width: 100%;
            padding: .1rem .1rem;

            ul {
                display: flex;
                justify-content: space-between;

                span {
                    margin-left: 1.5rem !important;
                }
            }
        }
    }
}
@media screen and (max-width: 580px) {
    .sidebar {
        ul {
            justify-content: space-around !important;

            li {
                padding: 1rem 0 !important;
            }
            i {
                font-size: 1.4rem !important;
                transform: translateY(-1px) !important;
            }
            span {
                display: none;
            }
            .active {
                
                color: $color-5 !important;
                background-color: rgba(51, 51, 51, 0) !important;
               
            }
        }
    }
}